// Colors
$color-primary: #0073aa;
$color-secondary: #15304b;
$color-accent: #00a0d2;
$color-accent-light: #c1f1ff;
$color-accent-light-transparent: rgba(193, 241, 255, 0.75);
$color-text: #333;
$color-light: #f8f9fa;
$color-dark: #23282d;
$color-border: #e2e4e7;

//  Gradients
$gradient-primary: linear-gradient(
    to bottom,
    $color-accent-light 0%,
    $color-accent-light 30%,
    $color-light 70%,
    $color-light 100%
);


// Typography
$font-body: 'Figtree', sans-serif;
$font-heading: 'Merriweather', serif;
$font-ui: 'Outfit', sans-serif;
$font-size-base: 16px;
$line-height-base: 1.5;

// Spacing
$spacing-xs: 5px;
$spacing-sm: 10px;
$spacing-md: 20px;
$spacing-lg: 40px;
$spacing-xl: 80px;

// Breakpoints
$breakpoint-xs: 480px;
$breakpoint-sm: 768px;
$breakpoint-md: 992px;
$breakpoint-lg: 1200px;

// Container widths
$container-width: 1140px;
$container-padding: 15px; 